<template>
  <div class="content">
    <div class="pay_way_group">
      <div class="pay_way_title">销量任务</div>
      <van-cell-group class="payment_group">
        <van-cell title="店铺名称">{{salesTask.goods.shop_name}}</van-cell>
        <van-cell title="关键字">{{salesTask.goods.key}}</van-cell>
        <van-cell title="拍下价格">{{salesTask.goods.price}}</van-cell>
        <div class="van-cell"><span>商品主图</span></div>
        <img v-lazy="salesTask.goods.main_img" width="100%" @click="showImagePreview(salesTask.goods.main_img)">
      </van-cell-group>
    </div>
    <div class="pay_way_group">
      <div class="pay_way_title">任务要求</div>
      <van-cell-group class="payment_group">
        <van-cell title="地区">{{salesTask.task.address}}</van-cell>
        <van-cell title="价格区间">{{salesTask.task.range}}</van-cell>
        <van-cell title="浏览时间">{{salesTask.task.browsing_time}}</van-cell>
        <van-cell title="是否收藏">{{salesTask.task.conllect}}</van-cell>
        <van-cell title="加购物车">{{salesTask.task.shopping_cart}}</van-cell>
        <van-cell title="是否假聊">{{salesTask.task.chat == '是' ? salesTask.task.chat_remark : '否'}}</van-cell>
        <van-cell v-if="salesTask.task.remark" title="备注">{{salesTask.task.remark}}</van-cell>
      </van-cell-group>
    </div>
    <div class="pay_way_group">
      <div class="pay_way_title">上传1个其他宝贝链接<span style="color: darkred; margin-left: 5px;">如何获取宝贝链接</span><van-icon @click="showHelp" slot="right-icon" name="question" class="van-cell__right-icon" /></div>
      <van-cell-group>
        <van-field v-model="salesTask.task.others1" readonly placeholder="请输入宝贝链接1" :error="errorInfo.others1"></van-field>
      </van-cell-group>
    </div>
    <div class="pay_way_group">
      <div class="pay_way_title">上传店内另一宝贝链接</div>
      <van-cell-group class="payment_group">
        <van-field v-model="salesTask.task.theShop" readonly type="text" placeholder="请输入宝贝链接" :error="errorInfo.theShop"></van-field>
      </van-cell-group>
    </div>
    <div class="pay_way_group">
      <div class="pay_way_title">订单验证</div>
      <van-cell-group class="payment_group">
          <van-field v-model="salesTask.task.orderNo" readonly type="text" placeholder="请输入您拍下的订单号" />
      </van-cell-group>
    </div>
    <div class="pay_way_group">
      <div class="pay_way_title">订单状态</div>
      <van-cell-group class="payment_group">
        <van-field v-model="salesTask.task.status_des" readonly type="text" />
      </van-cell-group>
    </div>
  </div>
</template>
<script>
  import {
    Row,
    Col,
    CellGroup,
    Cell,
    Button,
    Dialog,
    ImagePreview,
    Toast,
    NavBar,
    Field
  } from "vant";
  import { SALES } from "@/api/task";
  import img01 from '@/assets/images/img01.png'
  import img02 from '@/assets/images/img02.png'
  import img03 from '@/assets/images/img03.png'
  import img04 from '@/assets/images/img04.png'
  import img05 from '@/assets/images/img05.png'
  import img06 from '@/assets/images/img06.png'
  import img07 from '@/assets/images/img07.png'
  export default {
    name: "viewDetails",
    data() {
      return {
        isSubmit : false,
        salesTask: {
          "task": {
            "id": "",
            "type": "",
            "overtime": "",
            "conllect": "",
            "shopping_cart": "",
            "chat": "",
            "screenshot_pay": "",
            "screenshot_search": "",
            "address": "",
            "range": "",
            "browsing_time": "",
            "remark": "",
            "status_des": "",
            "others1" : '',
            "theShop" : '',
            "orderNo" : '',
          },
          "goods": {
            "id": "",
            "key": "",
            "main_img": "",
            "price": "",
            "shop_name": ""
          },
          "pick_details": [
            {
              "id": "",
              "front": "",
              "ending": "",
              "answer": ""
            }
          ],
          "pick_comments": [
            {
              "id": "",
              "comments": "",
              "answer": ""
            }
          ]
        },
        errorInfo: {
          shopName: false,
          others1:false,
          theShop: false,
          orderNo: false
        }
      };
    },
    props: {
      id: {
        // type : String,
        default: ''
      }
    },
    created(){
      this.initPage();
    },
    methods: {
      initPage(){
        if(!this.id){
          return;
        }
        let token = localStorage.getItem("token");
        this.$reqGet(SALES + this.id).then((res) => {
          this.getSuccess(res);
        }).catch((err) => {
          this.getSuccess(err);
        });
      },
      getSuccess(res){
        let data = res.data;
        if(!data || data.code != 0){
          let msg = data.msg ? data.msg : "获取任务失败";
          Toast.fail(msg);
          return;
        }
        this.salesTask = data.data;
      },
      showImagePreview(img){
        ImagePreview([img], 0);
      },
      showHelp() {
        ImagePreview([
          img01,
          img02,
          img03,
          img04,
          img05,
          img06,
          img07
        ]);
      }
    },
    components: {
      [Row.name]: Row,
      [Col.name]: Col,
      [CellGroup.name]: CellGroup,
      [Cell.name]: Cell,
      [Button.name]: Button,
      [ImagePreview.name]: ImagePreview,
      [Toast.name]: Toast,
      [NavBar.name]:NavBar,
      [Field.name]:Field
    }
  };

</script>

<style lang="scss" scoped>
  .task-countdown {
    text-align: center;
    font-size: 12px;
    background-color: #fffeec;
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    line-height: 30px;
    z-index: 999;
  }
  .pay_submit {
    position: fixed;
    bottom: 0;
    width: 100%;
  }
  .pay_way_group{
    background-color: #ffffff;
    margin-bottom: 10px;
  }
  .pay_way_group img {
    vertical-align: middle;
  }
  .pay_way_title {
    font-weight: bold;
    padding: 15px;
    background-color: #fff;
  }
  .upload_hint{
    position: relative;
    padding-left: 20px;
    line-height: 1.6;
    margin: 10px;
    i{
      position: absolute;
      top: 2px;
      left: 0;
    }
  }
  .vacancy_row{
    .van-col {
      font-size: 13px;
      line-height: 30px;
      text-align: center;
      vertical-align: middle;
      margin-bottom: 0px;
      background-clip: content-box;
    }
  }
  .countdown{
    text-align: center;
    width: 100%;
    height: 50px;
    line-height: 50px;
    border: 0;
    border-radius: 0;
    font-size: 16px;
    color: #fff;
    background-color: #29a0f6;
  }
</style>



